import returns from '../common/returns.vue'
import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
export default{
	components:{
		returns,
		uniLoadMore
	},
	data() {
		return {
			title:'拼团订单',
			show: 0,
			data:[],
			more:'more',
			page:1,
			loadRecord:true
		}
	},
	methods:{
		returns(){
			uni.switchTab({
			    url: '/pages/index/user'
			});
		},
		chiose(status){
			this.more = 'loading'
			this.data = ''
			this.show = status
			this.page = 1
			this.loadRecord = true
			this.service.entire(this,'get',this.APIconfig.api_root.subuser.g_order_lists,{
				user_id:this.$store.state.user.id,
				is_more:1,
				status:status,
				page:1
			},function(self,res){
				self.data = res.data.data
				if(res.data.data.length < 10){
					self.more = 'noMore'
					self.loadRecord = false
					return
				}
				self.more = 'more'
			})
		},
		loadMore(){
			let page = this.page
			this.more = 'loading'
			this.service.entire(this,'get',this.APIconfig.api_root.subuser.g_order_lists,{
				user_id:this.$store.state.user.id,
				is_more:1,
				status:this.show,
				page:page
			},function(self,res){
				console.log(res.data.data)
				let data = self.data
				
				data.push(...res.data.data)
				self.data = data
				self.page = page + 1
				if(res.data.data.length < 10){
					self.more = 'noMore'
					self.loadRecord = false
					return
				}
				self.more = 'more'
			})
		}
	},
	onReachBottom(){
		if(this.loadRecord == false) return
		this.loadMore()
	},
	onLoad(e) {
		this.show = e.status
	},
	onShow() {
		this.data.length = 0
		this.page = 1
		this.loadMore()
	}
}
